.card-wrap{margin:8px;border:1px solid #e8e8e8;font-size:14px}.card-wrap .card-icon{width:32px;height:48px;display:inline-block;background:url(data:image/png;base64,) no-repeat 0 0}.card-wrap .card-text-info,.card-wrap .card-text-normal{position:absolute;line-height:48px;font-size:14px}.card-wrap .card-text-normal{color:#999}.card-wrap .card-text-info{color:#bfbfbf;left:60px}.card-wrap .ant-card-head{height:48px;padding:0;position:relative}.card-wrap .ant-card-head-title{padding:0;height:48px}.card-content{margin:32px;text-align:left;font-size:14px;cursor:pointer}.card-content .content-name{color:#bfbfbf;margin-right:16px}.card-content .content-data{color:#262626}.card-content .content-desc{color:#d9d9d9}.card-content .margin-top-bottom{margin-top:24px;margin-bottom:0}.card-content .text-overflow{overflow:hidden;-o-text-overflow:ellipsis;text-overflow:ellipsis;white-space:nowrap}.ant-card-body{padding:0}